@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@400;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

/* General body and root styling */
body, html, #root {
  height: 100%;
  margin: 0;
  font-family: 'Libre Franklin', sans-serif;
  color: #f0f0f0; /* Apply the off-white color to the text */
}

body {
  position: relative;
}

.min-h-screen {
  min-height: 100vh;
}

.bg-transparent {
  background-color: transparent;
}

/* Responsive background size adjustments */
@media (max-width: 1024px) {
  body {
    background-size: 80%; /* Adjust size for smaller screens */
  }
}

@media (max-width: 768px) {
  body {
    background-size: 60%; /* Adjust size for smaller screens */
  }
}

@media (max-width: 480px) {
  body {
    background-size: 40%; /* Adjust size for smaller screens */
  }
}

/* Define keyframes for marquee animation */
@keyframes marquee {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-50%);
  }
}

/* Apply marquee animation */
.animate-marquee {
  display: flex;
  width: calc(200% + 8px); /* Ensures the loop runs seamlessly */
  animation: marquee 60s linear infinite; /* Adjust duration as needed */
}

/* Hide scrollbar for Chrome, Safari, and Opera */
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge, and Firefox */
.no-scrollbar {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

/* Marquee effect */
.marquee-container {
  overflow: hidden;
  white-space: nowrap;
}

.marquee {
  display: flex;
  animation: marquee 30s linear infinite;
}

.marquee div {
  display: inline-block;
}

/* Animated text */
@keyframes animate-text {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.animated-name {
  font-size: 2rem; /* Adjust font size as needed */
  font-weight: bold;
  background: linear-gradient(270deg, #ff6ec4, #7873f5, #76b852, #f5af19);
  background-size: 800% 800%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: animate-text 5s ease infinite;
}
